<template>
  <div class="scrlooBox">
    <div class="xaNav">
      <a href="javascript:void(0)"><i class="iconfont icon-dizhi"></i></a>
      <div class="homeAddr">
        <p><span>12312312312</span></p>
        <h2><span></span>C</h2>
        <h1>()</h1>
      </div>
      <a href="javascript:void(0)" @click.prevent="goback"><i class="iconfont icon-zuojiantou"></i></a>
    </div>
    <img class="images" src="../../../static/images/xiao/li.jpg"/>
    <div>
      <div class="tmallTitle">
        <img src="../../../static/images/xiao/logo.jpg"/>
        <p>{{pro.sellername}}</p>
      </div>
      <div class="tShirt">
        <img :src="pro.defaultImg"/>
        <div class="tShirtMes">
          <!--{{list.title}}-->
          <p>{{pro.title}}</p>
          <h1><span>{{gcolor}}</span>{{gsize}}<span></span></h1>
          <!--<h3 class="sevenBox"></h3>-->
          <div class="showPrice">
            <h2><span>{{gprice}}</span></h2>
            <span class="showNum">x{{totalNum}}</span>
          </div>
        </div>

      </div>
      <div class="buyNum">
        <p></p>
        <div class="clickNum">
          <button class="subBox" @click="sub(i)">-</button>
          <span class="Num">{{totalNum}}</span>
          <button class="addBox" @click="add(i)">+</button>
        </div>
      </div>
      <div class="buyProt">
        <p></p>
        <div class="buySend">
          <h2> <a href="javascript:void(0)"><i class="iconfont icon-zuojiantou"></i></a></h2>
        </div>
      </div>
      <div class="buyProt">
        <p></p>
        <div class="buySend">
          <h2>17.8 <a href="javascript:void(0)"><i class="iconfont icon-zuojiantou"></i></a></h2>
        </div>
      </div>
      <div class="buyProt">
        <p></p>
        <div class="buySend">
          <h2>EMS <a href="javascript:void(0)"><i class="iconfont icon-zuojiantou"></i></a></h2>
        </div>
      </div>
      <div class="griPro">
        <p></p>
        <input type="checkbox" class="checkBox" @click="go($event)"/>
        <h2 class="giveMon">107.2 </h2>
      </div>
      <div class="griPro">
        <p></p>
        <h4>?</h4>
      </div>
      <div class="griPro">
        <p></p>
        <input class="conCheck" type="text" placeholder=""/>
      </div>
      <div class="calc">
        <!--{{total.num}}-->
        <p> {{totalNum}}  <span>{{this.totalMoney}}</span></p>
      </div>
      <div class="splitDate">
        <div class="dataWrite">
          <p></p>
          <h2>600.00</h2>
        </div>
        <input type="checkbox" class="chooseBox"/>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    props:["sub","add","i","pro","totalMoney","totalNum"],
    data(){
      return{
        // num:7.2,
        total:0
      }
    },
    beforeMount(){
      // this.calctotal()
    },
    created () {
      console.log("pro",this.pro)
    },
    methods:{
      go(e){
        // console.log(e.target.checked)
        this.$emit("dm",e.target.checked)
      },
      calctotal(){
        this.total=this.totalMoney
      },
      goback(){
        window.history.back(-1)
      }
    },
    computed: {
      gcolor(){
        return this.$route.query.color
      },
      gsize(){
         return this.$route.query.size
      },
      gnum(){
        return this.$route.query.num
      },
      gprice(){
        return this.$route.query.price
      }
    }
  }

</script>

<style scoped>

</style>



