  <nav v-show="(say==1) && (sid==0)" >
    <div class="xqNav">
      <img class="xqNavImg1" :src="list[15].imageUrls[0]" >
      <div class="xqNavDiv">
        <span class="xqNavSpan1"><img class="xqNavImg2" src="../../../static/images/cheng/xiangqing5.gif" >  412399 U</span>
        <div>
          <span  class="xqNavSpan2"></span>
          <p class="xqNavP1"><span class="xqNavSpan5"></span><span class="xqNavSpan3">299</span> <span class="xqNavSpan">164</span> <span class="xqNavSpan"></span></p>
