        <span class="xqNavSpan1"><img class="xqNavImg2" src="../../../static/images/cheng/xiangqing5.gif" >{{p.title}}</span>
        <div>
          <span  class="xqNavSpan2"></span>
          <p class="xqNavP1"><span class="xqNavSpan5"></span><span class="xqNavSpan3">{{p.skuOptions[0].price}}</span> <span class="xqNavSpan">{{p.saleCount}}</span> <span class="xqNavSpan">{{p.storehouse}}</span></p>
          <p class="xqNavP2"><span class="xqNavSpan6">{{p.sellerScreenName}}</span> <span class="xqNavSpan4"> ></span> <span class="xqNavSpan4"></span></p>
        </div>
      </div>
    </div>
